<!-- 告警频繁TOP8 -->
<template>
<div class="content">
	<div class="container" style=""></div>
</div>
</template>

<script>
function initChart(ele, myData){
	let myChart ;
  let dom = ele.querySelector('.container');
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}
  var colorList = ["#6a191c", "86262a", "#b6353a", "#d46559", "#e29856", "#eda639", "#f6cb4a", "#fceca6"];

  let option = null;
  option = {
    title: {
      text: myData.title,
      textStyle:{
        color:'#00fff0',
        fontSize: 30,
      },
      left:40,
      top:10
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      }
    },
    grid: {
      left: '10%',
      top:"10%",
      right: '8%',
      bottom: '0%',
      containLabel: true
    },
    xAxis: {
      type: 'value',
      show:false
    },
    yAxis: {
      type: 'category',
      axisLine:{
        show:false
      },
      axisTick:{
        show:false
      },
      axisLabel:{
        show:true,
        margin:40,
        color:"#fbfbfb",
        fontSize:20
      },
      data: myData.xList.reverse()
    },
    series: [
      {
        type: 'bar',
        barWidth:30,
        data: myData.dataList.reverse().map(function(item, index) {
          // console.log( item, index)
          return {
            value: item,
            itemStyle:{
              color:  colorList[index%9],
              barBorderRadius:15
            }
          }
        })       
      }
    ]
  };

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
	}
} 
    


export default {
	name: '',
	props: {
    static: {
      default(){
        return {
          title: "--",
          xList: ['老丰阁1','老丰阁2','老丰阁3','老丰阁4','老丰阁','老丰阁',"老丰阁","老丰阁"],
          "dataList": [100, 90,80,70,60,50,40,30]
        }
      }
    }
	},
	data () {
		return {

		}
	},
	mounted(){
	 this.update();
	},
	watch: {
		static: function (val, oldV) {
      this.update();
		}
	},
  methods: {
    update(){
      initChart(this.$el, this.static);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
	height: 516px;
	width:538px;
}
.content{
	margin:0;
	border-radius: 8px;
	height: 512px;
	width:538px;
	background-color:#001122;
  border: 1px solid rgba(56, 174, 225, 0.25);
}
</style>



