<template>
<div class="content">
	<div class="title">
		<img class="imgtit" src="static/img/Greening_watch.png" />
		<span class="titlespan">{{static.title}}</span>
	</div>
	<div class="flex">
		<div class="flex_item" v-for="item in static.dataList">
			<img class="pic" :src="'static/img/'+item.pic">
			<p class="text">{{item.name}}</p>
			<p class="num">{{item.num}}<span class="sub_txt">{{item.unit}}</span></p>
		</div>
		
	</div>
</div>
</template>

<script>
export default {
	name: '',
	props: {
		static: {
			default(){
				return {
					"title": "智能垃圾桶告警数",
					"dataList": [
						{
							"name": "满溢",
							"num": 123,
							"pic": "Greening_manyi.png",
							"unit": "次"
						},
						{
							"name": "乱放",
							"num": 123,
							"pic": "Greening_luanfang.png",
							"unit": "次"
						},
						{
							"name": "温度",
							"num": 123,
							"pic": "Greening_wendu.png",
							"unit": "次"
						}
					]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.content{
	margin:0;
	height: 100%;
	width: 100%;
}

.title{			
	font-size: 30px;			
	color:#00ffee;
	padding: 10px 20px;
	.titlespan, .imgtit{
		vertical-align: middle;
	}
}
.flex{
	justify-content: space-around;
}
.flex_item{
	padding-top: 70px;
	flex: 0 0 150px;
	height: 40px;
	text-align: left;
	P{
		white-space: nowrap;
	}
	.text{
		padding-bottom: 10px ;
		color: #00fff0;
		font-size: 24px;
	}
	.num{
		color: #f84242;
		font-size: 30px;
	}
	.sub_txt{
		padding-left: 10px;
		font-size: 20px;
		color: #eaeaea;
	}
}
.pic{
	height: 80px;
	float: left;
	padding-right: 10px;
}

</style>


