<!-- 食品安全 -->
<template>
	<page-box class="community">
		<!-- 左边 -->
		<div class="c_left clear" slot="left">
			<div class="left_1 clear">
				<todayWarning :static="static.left0101"></todayWarning>
				<deploy
					:static="static.left0102"
				></deploy>
			</div>
			<div class="left_2">
				<div class="left_2_1">
					<warn-sum
						class="warn_sum_sm"
						:num="static.left02.num"
						:static="static.left02"
					></warn-sum>
				</div>
				<div class="left_2_2">
					<flexHeng
						:static="static.left02.left0202"
					></flexHeng>
				</div>
				<div class="left_2_3">
					<div class="com_title">{{static.left02.left0203.title}}</div>
					<div class="chart"></div>
				</div>
			</div>
			<div class="left_3">
				<div class="left_3_1">
					<warnGarbage
						:static="static.left0301"
					></warnGarbage>
				</div>
				<div class="left_3_2">
					<div class="com_title">{{static.left0302.title}}</div>
					<table class="mytable01">
						<tbody>
							<tr>
								<th v-for="item in static.left0302.thList">{{item}}</th>
							</tr>
							<tr v-for="item in static.left0302.dataList">
								<td>{{ item[0] }}</td>
								<td>{{ item[1] }}</td>
								<td>{{ item[2] }}</td>
								<td>{{ item[3] }}</td>
								<td>{{ item[4] }}</td>
								<td>{{ item[5] }}</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
		<!-- 中间 -->
		<div class="c_center clear" slot="center">
			<div class="center_1">
				<iframe :src="static.centerTop01.iframe" class="cont"></iframe>
			</div>
			<div class="center_2">
				<warnAreaBar
					:list="static.center02.dataList"
					:static="static.center02"
				></warnAreaBar>
			</div>
			<div class="center_3">
				<warnAreaBarh
					:list="static.center03.dataList"
					:static="static.center03"
				></warnAreaBarh>
			</div>
		</div>
		<!-- 右边 -->
		<div class="c_right clear" slot="right">
			<div class="right_1">
				<div class="com_title">{{static.right01.title}}</div>
				<div class="chart"></div>
			</div>
			<div class="right_2">
				<warn-occur-time
					:list="static.right02.dataList"
					:static="static.right02"
				></warn-occur-time>
			</div>
			<div class="right_3">
				<div class="com_title"><i class="icon"></i>{{static.right03.title }}</div>
				<com-table01
					:thList="static.right03.thList"
					:list="static.right03.dataList"
				></com-table01>
			</div>
		</div>
	</page-box>
</template>

<script>

import PageBox from '@/modules/page-box.vue';

import todayWarning from '@/components/todayWarning.vue';
import deploy from '@/components/deploy.vue';
import warnSum from '@/components/warnSum.vue';
import flexHeng from '@/components/flexHeng.vue';
import warningTop8 from '@/components/warningTop8.vue'
import warnGarbage from '@/components/warnGarbage.vue'

import warnAreaBar from '@/components/warnAreaBar.vue';
import warnAreaBarh from '@/components/warnAreaBarh.vue';

import warnOccurArea from '@/components/warnOccurArea.vue';
import warnOccurTime from '@/components/warnOccurTime.vue';
import tableT02 from '@/components/tableT02.vue';
import comTable01 from '@/components/comTable01.vue';

// echart
import {initBarh2} from '@/charts/barh2.js';
import {initBar1} from '@/charts/bar1.js';

// js
import {insertScript} from '@/assets/js/assets.com.js';
import {formatChartList} from '@/assets/js/assets.com.js';

// 数据
import pageJson from '@/../static/pageJson/garbage.json';


export default {
	name: '',
	components: {
		PageBox,
		todayWarning, deploy, warnSum, flexHeng, warningTop8,warnGarbage,
		warnAreaBar, warnAreaBarh,
		warnOccurArea, warnOccurTime, tableT02, comTable01
	},
	data () {
		return {
		  static: pageJson,
		  timer: '', // 计时器
		}
	},
	mounted(){
		var timerI = 0;
		var _vue = this;

		_vue.queryData(timerI);

		this.timer = setInterval(function () {
			console.log( 'community', timerI )
		  _vue.queryData(timerI);

		  timerI++;
		  timerI%=5;
		}, 30000);
	},
	beforeRouteLeave (to, from, next){
		clearInterval( this.timer );
		next();
	},
	methods: {
		updata(){
			// 更新数据的操作放着里
			// 组件更新为监听数据变化

			// 清理次数TOP5
			var list1 = formatChartList(this.static.left02.left0203.dataList );
			initBarh2(this.$el.querySelector('.left_2_3 .chart'), {
				xList: list1.xList,
				dataList: list1.data1
				},{
				grid: {
				  left: '10%',
				  top:"0%",
				  right: '10%',
				  bottom: '5%',
				  containLabel: true
				}
			});

			// 垃圾堆放次数排名
			var list2 = formatChartList(this.static.right01.dataList );
			initBar1(this.$el.querySelector('.right_1 .chart'), {
				xList: list2.xList,
				data1: list2.data1,
				data2: list2.data2,
				},{
				grid: {
				  left: '5%',
				  top:"10%",
				  right: '5%',
				  bottom: '8%',
				  containLabel: true
				},
			});
		},
		queryData(index){
			var _vue = this;
			
			insertScript( `./static/data/garbage/data${index}.js` );
			// $('#myScript').html( `'<script data-type=static src=""><\/script>'` );
			_vue.static = $.extend(true, {}, pageJson, staticData );
			_vue.updata();
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import url(../../../assets/less/var.less);
.community{
	div{
		box-sizing: border-box;
	}
	.com_title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 40px;
	}
	// 坐边
	.c_left{
		padding-left: 40px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.left_all(){
			overflow: hidden;
			box-sizing: border-box;
		}
		.left_1{
			.left_all();
			width: 100%;
			height: 265px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.left_2{
			.left_all();
			width: 400px;
			height: 675px;
			border: solid 1px rgba(56, 174, 225, 0.71);
			border-radius: 8px;
			.left_2_1{
			}
			.left_2_2{
				
			}
		}
		.left_3{
			.left_all();
			width: 800px;
			padding-left: 15px;
			height: 680px;
		}
		.left_3_1, .left_3_2{
			box-sizing: border-box;
			border: solid 1px rgba(56, 174, 225, 0.71);
			width: 100%;
			height: 330px;
			margin-bottom: 15px;
			border-radius: 8px;
		}
	}
	// 中间
	.c_center{
		padding: 0 20px 0 20px;
		>div{
			float: left;
		}
		.center_1{
			width: 1390px;
			height: 600px;
			margin-bottom: 20px;
			border-radius: 8px;
			// border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.center_2{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
			margin-right: 10px;
		}
		.center_3{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
		}
	}
	// 右边
	.c_right{
		height: 100%;
		width: 100%;
		padding-right: 30px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.right_1{
			width: 570px;
			height: 510px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
		.right_2{
			width: 555px;
			height: 510px;
			margin-bottom: 15px;
			margin-left: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
		.right_3{
			width: 1140px;
			height: 430px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
	}

	// 左边内容
	.left_1{
		>div{
			float: left;
		}
	}
	.left_2_1{
		>.content{
			height: 200px !important;
			>.title{
				margin-bottom: 20px !important;
			}
		} 
	}
	.left_2_3{
		width: 100%;
		
		.chart{
			height: 300px;
		}
	}
	.center_2{

	}
	.center_2{
		padding-right: 10px;
		overflow: hidden;
	}
	

	// 中间内容
	.center_1{
		position: relative;
		img{
			position: absolute;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
		}
	}
	.center_2{
		.chart{
			height: 250px;
		}
	}
	.center_3{
		.center_2();
	}
	.mytable01{
		.mytable01();
		th{
			font-size: 14px;
			color: #eaeaea;
		}
		td{
			font-size: 18px;
			color: #d5b930;
		}
	}
	.right_1{
		.chart{
			margin-top: 20px;
			height: 420px;
			width: 90%;
			margin-left: 5%;
		}
	}
}

</style>



