
export function initBar1 (dom, list, outOpt){
	// var myData = formatList(list);
	var myChart ;
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}

	var option = null;

	option = {
		title: {

		},
		grid: {
			top: '10%',
			left: '7%',
			right: '0%',
		},
		tooltip: {
			trigger: 'axis',
			axisPointer: {
				type: 'shadow'
			},
			formatter: '{b1}:{c1}'
		},
		xAxis: {
			data: list.xList,
			axisLabel: {
				inside: false,
				textStyle: {
					color: '#fff',
					fontSize: 16
				}
			},
			axisTick: {
				show: false
			},
			axisLine: {
				show: true
			},
			z: 10,
		},
		yAxis: {
			splitLine: {　
				show: false,
			},
			axisLine: {
				show: true
			},
			axisTick: {
				show: false
			},
			axisLabel: {
				show: true,
				textStyle: {
					color: '#fff',
					fontSize: 14
				}
			}
		},
		series: [{ // For shadow
			type: 'bar',
			itemStyle: {
				normal: {
					color: '#202126'
				}
			},
			barGap: '-100%',
			barCategoryGap: '60%',
			data: list.data2,
			animation: false
		}, {
			type: 'bar',
			itemStyle: {
				normal: {
					color: new echarts.graphic.LinearGradient(
						0, 0, 0, 1, [{
							offset: 0,
							color: '#2ea6db'
						}, {
							offset: 0.5,
							color: '#496bce'
						}, {
							offset: 1,
							color: '#5a1a74'
						}]
					)
				},
			},
			data: list.data1
		}]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option,  true);
		myChart.setOption(outOpt, false);
	}
}




