<template>
<div class="flex">
	<div class="flex_item" 
		v-for="item in static.dataList"
	>
		<p class="flex_top"><img class="icon" 
			:src="'static/img/'+item.icon">{{item.name}}</p>
		<p class="num">{{item.num}}</p>
	</div>
</div>
</template>

<script>
export default {
	name: '',
	props: {
		static: {
			default(){
				return {
					dataList: [
						{
							"icon": 'Greening_gaojing1.png',
							"name": "清理次数",
							"num": 1
						},
						{
							"icon": 'Greening_gaojing2.png',
							"name": "清理次数",
							"num": 1
						},
						{
							"icon": 'Greening_gaojing3.png',
							"name": "清理次数",
							"num": 1
						}
					]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.flex{
	justify-content: space-around;
	justify-items: center;
}
.flex_item{
	flex: 1 1 auto;
	margin-right: 5px;
	background-color: #0d2740;
	border-radius: 10px;
	border: solid 1px #274253;
	&:last-child{
		margin-right: 0px;
	}
}
.icon{
	width: 30px;
	height: 30px;
	padding: 0 5px;
	vertical-align: middle;
}
.num{
	text-align: center;
	font-size: 30px;
	padding-bottom: 10px;
}
.flex_top{
	overflow: hidden;
	white-space: nowrap;
	color: #eaeaea;
	padding: 10px 0;
	font-size: 18px;
}
</style>


